// CALCULATOR-----------------------------------------------------
export default function calculator() {
	const result = document.querySelector('.calculating__result span'); //    ,      
	let sex, height, weight, age, ratio = 1.375; //    ( let     ): , , ,    
	if (localStorage.getItem('sex')) { //        localStorage
		sex = localStorage.getItem('sex'); //    sex  localStorage
	} else {
		sex = 'female'; //     sex  localStorage,    
		localStorage.setItem('sex', 'female'); //   localStorage   
	}
	if (localStorage.getItem('ratio')) { //        localStorage
		ratio = localStorage.getItem('ratio'); //    ratio  localStorage
	} else {
		ratio = 1.375; //     ratio  localStorage,    
		localStorage.setItem('ratio', 1.375); //   localStorage   
	}
	function initLocalSettings(selector, activeClass) { //     sex  ratio,      localStorage  sex: female  ratio: 1.375,   
		const elements = document.querySelectorAll(selector); //    elements
		elements.forEach(elem => { //     sex  ratio
			elem.classList.remove(activeClass); //      
			if (elem.getAttribute('id') === localStorage.getItem('sex')) { //   id   localStorage  sex: female    
				elem.classList.add(activeClass);
			}
			if (elem.getAttribute('data-ratio') === localStorage.getItem('ratio')) { //   id   localStorage  ratio: 1.375    
				elem.classList.add(activeClass);
			}
		});
	} //            !!!!
	initLocalSettings('#gender div', 'calculating__choose-item_active');
	initLocalSettings('.calculating__choose_big div', 'calculating__choose-item_active');
	function calcTotal() { //   ,          ,     
		if (!sex || !height || !weight || !age || !ratio) { //  (+'fbgdfj')       NaN,  NaN == false;   : true, ..   =>
			result.textContent = ''; // =>   ,     switch   (+input.value;);        NaN == false,  =>
			return; // =>      '...'     return   !!!
		}
		if (sex === 'female') {
			result.textContent = Math.round((447.6 + (9.2 * weight) + (3.1 * height) - (4.3 * age)) * ratio); //  Math.round() -     
		} else {
			result.textContent = Math.round((88.36 + (13.4 * weight) + (4.8 * height) - (5.7 * age)) * ratio); //  Math.round() -     
		}
	}
	calcTotal();
	function getStaticInformation(selector, activeClass) { //      :     ,      
		const elements = document.querySelectorAll(selector); //   ( div)   #gender  .calculating__choose_big
		elements.forEach(elem => {
			elem.addEventListener('click', (e) => { //      ,    div ( )    
				if (e.target.getAttribute('data-ratio')) { //    - ratio (..   data-ratio),     data-ratio , 
					ratio = +e.target.getAttribute('data-ratio'); //   ratio    data-ratio
					localStorage.setItem('ratio', +e.target.getAttribute('data-ratio')); //   localStorage  ,  
				} else {
					sex = e.target.getAttribute('id'); //   - gender/sex,     id
					localStorage.setItem('sex', e.target.getAttribute('id')); //   localStorage  ,  
				}
				console.log(ratio, sex);
				elements.forEach(elem => { //   
					elem.classList.remove(activeClass);
				});
				e.target.classList.add(activeClass);
				calcTotal();
			});
		});
		// function getStaticInformation(parentSelector, activeClass) { //      :      ,      
		// 	const elements = document.querySelectorAll(`${parentSelector} div`); //   ( div)   
		// document.querySelector(parentSelector).addEventListener('click', (e) => { //      ,    div ( )    
		// 	if (e.target.getAttribute('data-ratio')) { //    - ratio (..   data-ratio),     data-ratio , 
		// 		ratio = +e.target.getAttribute('data-ratio'); //   ratio    data-ratio
		// 	} else {
		// 		sex = e.target.getAttribute('id'); //   - gender/sex,     id 
		// 	}
		// 	console.log(ratio, sex);
		// 	elements.forEach(elem => { //   
		// 		elem.classList.remove(activeClass);
		// 	});
		// 	e.target.classList.add(activeClass);
		// 	calcTotal();
		// }); // !!!      ,     ,  ,      !!!
		// }
	}
	getStaticInformation('#gender div', 'calculating__choose-item_active');
	getStaticInformation('.calculating__choose_big div', 'calculating__choose-item_active');
	function getDynamicInformation(selector) { //     input
		const input = document.querySelector(selector);
		input.addEventListener('input', () => { //  switch case 
			if (input.value.match(/\D/g)) { //     input  value   ,     !!!
				input.style.border = '2px solid red'; //       input
			} else {
				input.style.border = 'none'; //   /   !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
			}
			switch(input.getAttribute('id')) { //  input   
			case 'height': //   ,      
				height = +input.value;
				break;
			case 'weight': //   ,      
				weight = +input.value;
				break;
			case 'age': //   ,      
				age = +input.value;
				break;
			}
			calcTotal();
		});
	}
	getDynamicInformation('#height');
	getDynamicInformation('#weight');
	getDynamicInformation('#age');
}