/* eslint-disable linebreak-style */
export default function carousel({container, slide, nextArrow, prevArrow, totalCounter, currentCounter, wrapper, field}) { //  ,   
	// SLIDER---------------------- --------------------
	// const slides = document.querySelectorAll('.offer__slide'); //     
	// const prev = document.querySelector('.offer__slider-prev'); //    
	// const next = document.querySelector('.offer__slider-next'); //    
	// const totalSlides = document.querySelector('#total'); //     
	// const currentSlide = document.querySelector('#current'); //     
	// let slideIndex = 1; //    
	// showSlides(slideIndex); //   showSlides()   "1"
	// if (slides.length < 10) { //    
	// 	totalSlides.textContent = `0${slides.length}`; //       "0"
	// } else { // 
	// 	totalSlides.textContent = slides.length; //     
	// }
	// function showSlides(n)  { //      "n"
	// 	if (n > slides.length) {//    slides.length     "n"
	// 		slideIndex = 1; //    " " ,     "" => slideIndex = 1
	// 	}
	// 	if (n < 1) {//     "n"  1
	// 		slideIndex = slides.length; //    " " ,     "" => slideIndex = slides.length
	// 	}
	// 	slides.forEach(item => item.style.display = 'none'); //      
	// 	slides[slideIndex - 1].style.display = 'block' ; //      ,  [slideIndex - 1]      "0"
	// 	if (slides.length < 10) { //    
	// 		currentSlide.textContent = `0${slideIndex}`; //       "0"
	// 	} else { // 
	// 		currentSlide.textContent = slideIndex; //     
	// 	}
	// }
	// function plusSlide(n) { //      
	// 	showSlides(slideIndex += n); //  n=1,  ,  n=-1,   
	// }
	// prev.addEventListener('click', () => { //     "",    plusSlide()  
	// 	plusSlide(-1);
	// });
	// next.addEventListener('click', () => { //     "",    plusSlide()  
	// 	plusSlide(1);
	// });
	
	// CAROUSEL-------------------------------------  ------------
	const slides = document.querySelectorAll(slide); //      (length: 4)
	const slider = document.querySelector(container); //    
	const prev = document.querySelector(prevArrow); //    
	const next = document.querySelector(nextArrow); //    
	const totalSlides = document.querySelector(totalCounter); //      
	const currentSlide = document.querySelector(currentCounter); //      
	const slidesWrapper = document.querySelector(wrapper); //  - 
	const sliderInner = document.querySelector(field); //    ,     
	const sliderWidth = window.getComputedStyle(slidesWrapper).width; //      -  (     ) = 650px
	let slideIndex = 1; //    	
	let slideOffset = 0; //      
	if (slides.length < 10) { //    
		totalSlides.textContent = `0${slides.length}`; //       "0"
		currentSlide.textContent = `0${slideIndex}`;
	} else { // 
		totalSlides.textContent = slides.length; //     
		currentSlide.textContent = slideIndex;
	}
	sliderInner.style.width = 100 * slides.length + '%'; // 100%    offer__slide     slides.length (  css ),      offer__slider-inner ( 400%)
	sliderInner.style.display = 'flex'; //  CSS   offer__slider-inner  ,     
	sliderInner.style.transition = '0.5s all'; //  CSS   offer__slider-inner  ,    
	slidesWrapper.style.overflow = 'hidden'; //     offer__slider-wrapper
	slides.forEach(slide => { //    ,      ,   
		slide.style.width = sliderWidth; //   650px
	});
	slider.style.position = 'relative'; //     offer__slider
	const dots = document.createElement('ol'); //   dots       ol  
	const dotsArr = []; //       (length: 4) [li, li, li, li]
	dots.classList.add('carousel-dots'); //    ol    carousel-dots  CSS 
	dots.style.cssText = `
		position: absolute;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 15;
		display: flex;
		justify-content: center;
		margin-right: 15%;
		margin-left: 15%;
		list-style: none;
	`;
	slider.append(dots); //     ol  
	for (let i = 0; i < slides.length; i++) { //   +1    
		const dot = document.createElement('li'); //  li -     =>
		dot.setAttribute('data-slide-to', i + 1); //  , ..  (    + 1,    )  CSS 
		dot.style.cssText = `
			box-sizing: content-box;
			flex: 0 1 auto;
			width: 30px;
			height: 6px;
			margin-right: 3px;
			margin-left: 3px;
			cursor: pointer;
			background-color: #fff;
			background-clip: padding-box;
			border-top: 10px solid transparent;
			border-bottom: 10px solid transparent;
			opacity: .5;
			transition: opacity .6s ease;
		`;
		if (i == 0) { //    =>
			dot.style.opacity = 1; //    dot    (opacity) 
		}
		dots.append(dot); //       ol  ,    
		dotsArr.push(dot); //    ()     (push -   )
	}
	function deleteNotDigits(str) { //    
		return +str.replace(/\D/g, ''); //    (+str)       (\D)    (g)    , .. 650px => 650
	}
	function changeIndexondition() { //      
		if (slides.length < 10) {
			currentSlide.textContent = `0${slideIndex}`;
		} else {
			currentSlide.textContent = slideIndex;
		}
	}
	function changeDotondition() { //    
		dotsArr.forEach(dot => dot.style.opacity = '.5');
		dotsArr[slideIndex - 1].style.opacity = 1;
	}
	prev.addEventListener('click', () => { //     "",        slideOffset
		if (slideOffset == 0) { //    ,      ,    
			slideOffset = deleteNotDigits(sliderWidth) * (slides.length - 1); // ..            -      (  '650px'    5 -   )   (   ) = 1950
			sliderInner.style.transition = '0.25s all';
		} else {
			slideOffset -= deleteNotDigits(sliderWidth); //    "",  -slideOffset           
			sliderInner.style.transition = '0.5s all';
		}
		sliderInner.style.transform = `translateX(-${slideOffset}px)`; //     transform: translateX(),     -   
		if (slideIndex == 1) { //   slideIndex  1 
			slideIndex = slides.length; //   slideIndex  
		} else {
			slideIndex--; //    
		}
		changeIndexondition();
		changeDotondition();
	});
	next.addEventListener('click', () => { //     "",       slideOffset 
		if (slideOffset == deleteNotDigits(sliderWidth) * (slides.length - 1)) { //      (  '650px'    5 -   )   (   )
			sliderInner.style.transition = '0.25s all';
			slideOffset = 0; // ..           
		} else {
			sliderInner.style.transition = '0.5s all';
			slideOffset += deleteNotDigits(sliderWidth); //     "",  +slideOffset           
		}
		sliderInner.style.transform = `translateX(-${slideOffset}px)`; //     transform: translateX(),     -   
		if (slideIndex == slides.length) { //   slideIndex   
			slideIndex = 1; //   slideIndex 
		} else {
			slideIndex++; //    
		}
		changeIndexondition();
		changeDotondition();
	});
	dotsArr.forEach(dot => {
		dot.addEventListener('click', (e) => { //     
			const slideTo = e.target.getAttribute('data-slide-to'); //   slideTo   data-slide-to
			slideIndex = slideTo; //   slideIndex  slideTo
			slideOffset = deleteNotDigits(sliderWidth) * (slideTo - 1);
			sliderInner.style.transform = `translateX(-${slideOffset}px)`;
			changeIndexondition();
			changeDotondition();
		});
	});
}