const postData = async (url, data) => { // function expression -      , postData         + async     
	const result = await fetch(url, { //  fetch(), url -     , data - ,    - ..    + await     
		method: 'POST',
		headers: {
			'Content-type': 'application/json' 
		},
		body: data,	//        fetch(),     !!!	
	}); //    ,   result  ,       
	return await result.json(); //    postData  (result.json())      .then() -     + await     result.json()!!!
};
const getResources = async (url) => { // function expression -     , getResources       + async     
	const result = await fetch(url); //    ,   result  ,       ,  fetch  404, 403, 401    !!! 
	//           !!!     :
	if (!result.ok) { //   result -  .... 
		throw new Error(`Could not fetch ${url}, status: ${result.status}`); //    
	}
	return await result.json(); //    postData  (result.json())      .then() -     + await     result.json()!!!
};
export {postData};
export {getResources};