import 'es6-promise'; //    node_modules    package.json
import 'nodelist-foreach-polyfill'; //    node_modules    package.json

import tabs from './modules/tabs.js';
import cards from './modules/cards.js';
import timer from './modules/timer.js';
import modal from './modules/modal.js';
import forms from './modules/forms.js';
import carousel from './modules/carousel.js';
import calculator from './modules/calculator.js';
import {openModalWindow} from './modules/modal.js';

window.addEventListener('DOMContentLoaded', () => {

	const modalTimerId = setTimeout(() => openModalWindow('.modal', modalTimerId), 60000); //    

	// TABS-----------------------------------------------------------
	tabs('.tabheader__item', '.tabcontent', '.tabheader__items', 'tabheader__item_active'); //     tabs()    : =>
	// tabs(tabsSelector, tabsContentSelector, tabsParentSelector, activeClass)

	// TAIMER-( )-------------------------------------
	timer('.timer', '2024-12-31T24:00:00.000+03:00'); //  YYYY-MM-DDTHH:mm:ss.sss GMT+3,  -  TIME!!!
	// timer(id, deadLine)

	// MODAL----------------------------------------------------------	
	modal('[data-modal]', '.modal', modalTimerId);
	// modal(triggerSelector, modalSelector, modalTimerId)

	// CLASSES-for-CARDS----------------------------------------------
	cards();

	// SEND-FORMS----fetch()       
	forms('form', modalTimerId);

	// SLIDER---------------------- --------------------
	carousel({ //  ,   
		container: '.offer__slider',
		slide: '.offer__slide',
		nextArrow: '.offer__slider-next',
		prevArrow: '.offer__slider-prev',
		totalCounter: '#total',
		currentCounter: '#current',
		wrapper: '.offer__slider-wrapper',
		field: '.offer__slider-inner',
	});

	// CALCULATOR-----------------------------------------------------
	calculator();

});