// TAIMER-( )-------------------------------------
export default function timer(id, deadLine) { // timer('.timer', '2024-03-25T11:57:00.000+03:00');

	// const deadLine = '2023-12-31'; //    ,      ... setClock('.timer', deadLine);
	function getTimeRemaining(endTime) { //    
		let days, hours, minutes, seconds;
		const t = Date.parse(endTime) - Date.parse(new Date()); //    deadLine (endTime)    (new Date())  
		// console.log(Date.parse(new Date())); // : 1711099417000 ,  Date.parse() -    
		// console.log(new Date()); // Fri Mar 22 2024 12:21:51 GMT+0300 (,  )
		if (t <= 0) { //  t  ,        
			days = 0;
			hours = 0;
			minutes = 0;
			seconds = 0;
		} else {		
			days = Math.floor(t / (1000 * 60 * 60 * 24)); //    , Math.floor() -     ( *  *  *   )!
			hours = Math.floor((t / (1000 * 60 * 60) % 24)); //    , % -   ,  50 / 24 = 2   2 ,  2 !
			minutes = Math.floor((t / 1000 / 60) % 60); //     
			seconds = Math.floor((t / 1000) % 60); //     
		}
		return { //    getTimeRemaining()        !!!
			'total': t,
			'days': days,
			'hours': hours,
			'minutes': minutes,
			'seconds': seconds,
		};
	}
	function getZero(num) { //   0       0   !!!
		if (num >= 0 && num < 10) {
			return `0${num}`; //          -    
		}	else if (num < 0) {
			return '00'; //    -   ,   
		}	else {
			return num; //      
		}
	}
	function setClock(selector, endTime) { //     
		const timer = document.querySelector(selector); //  div.timer   setClock(id, deadLine) === timer('.timer', '2024-03-25T11:57:00.000+03:00')
		const days = timer.querySelector('#days');
		const hours = timer.querySelector('#hours');
		const minutes = timer.querySelector('#minutes');
		const seconds = timer.querySelector('#seconds');
		const timeInterval = setInterval(updateClock, 1000); //  timeInterval()    1000   ===  updateClock(),    setInterval()
		updateClock(); //     ,    ,   setInterval  1000 
		function updateClock () { //     ,      
			const t = getTimeRemaining(endTime); //     ,        getTimeRemaining()
			days.innerHTML = getZero(t.days); //    HTML
			hours.innerHTML = getZero(t.hours); //    HTML
			minutes.innerHTML = getZero(t.minutes); //    HTML
			seconds.innerHTML = getZero(t.seconds); //    HTML
			if (t.total <= 0) {
				clearInterval(timeInterval); //      ,  (new Date())   (endTime), ..   
			}
		}
	}
	setClock(id, deadLine);
	function showEndTime(endTime) {
		const time = new Date(endTime);
		const endDate = document.querySelector('.promotion__end-date');
		const yearBlock = endDate.querySelector('#year');
		const monthBlock = endDate.querySelector('#month');
		const dayBlock = endDate.querySelector('#day');
		const hourBlock = endDate.querySelector('#hour');
		const minBlock = endDate.querySelector('#min');
		yearBlock.innerHTML = getZero(time.getFullYear());
		monthBlock.innerHTML = getZero(time.getMonth() + 1);
		dayBlock.innerHTML = getZero(time.getDate());
		hourBlock.innerHTML = getZero(time.getHours());
		minBlock.innerHTML = getZero(time.getMinutes());
	} 
	showEndTime(deadLine); // 2024-03-25T00:00:00.000+03:00

}