// MODAL----------------------------------------------------------
function openModalWindow(modalSelector, modalTimerId) {
	const modalWindow = document.querySelector(modalSelector);
	modalWindow.classList.add('show');
	modalWindow.classList.remove('hide');
	document.body.style.overflow = 'hidden'; //    ,   
	// console.log(modalTimerId);
	if (modalTimerId) { //  modalTimerId  ,      clearInterval()
		clearInterval(modalTimerId); //      ,    
	}	
}
function closeModalWindow(modalSelector) {
	const modalWindow = document.querySelector(modalSelector);
	modalWindow.classList.add('hide');
	modalWindow.classList.remove('show');
	document.body.style.overflow = ''; //     ,   
}
function modal(triggerSelector, modalSelector, modalTimerId) { // => modal('[data-modal]', '.modal', modalTimerId);    triggerSelector, modalSelector   
	const modalTrigger = document.querySelectorAll(triggerSelector); // '[data-modal]'
	const modalWindow = document.querySelector(modalSelector); // '.modal'
	// const modalCloseBtn = document.querySelector('[data-close]'); //      
	modalTrigger.forEach(btn => {
		btn.addEventListener('click', () => openModalWindow(modalSelector, modalTimerId)); //       openModalWindow(modalSelector))    ,   , () =>         
	});
	// modalCloseBtn.addEventListener('click', closeModalWindow); //      
	modalWindow.addEventListener('click', (e) => {
		if (e.target === modalWindow || e.target.getAttribute('data-close') == '') { //     ( )    ,    
			closeModalWindow(modalSelector); //       || e.target.getAttribute('data-close') == '' ..     data-close    ,      -  		
		}
	});
	document.addEventListener('keydown', (e) => { //     
		if (e.code === 'Escape' && modalWindow.classList.contains('show')) { //  code === 'Escape'  keydown -     ESC,    
			closeModalWindow(modalSelector); //  modalWindow.classList.contains('show'))     ,    closeModalWindow()
		}
	});
	function showModalWindowByScroll() {
		if (window.scrollY + document.documentElement.clientHeight >= document.documentElement.scrollHeight - 1) { //      Y   +      /     
			openModalWindow(modalSelector, modalTimerId); //   ,       =>   ,    !!!
			window.removeEventListener('scroll', showModalWindowByScroll); //      ,      !!!     removeEventListener()!!!
		} //    ,  =>
	} // }, {once: true});     ,      1px   !!!
	window.addEventListener('scroll', showModalWindowByScroll); //   scroll    
}
export default modal;
export {openModalWindow};
export {closeModalWindow};