// TABS-----------------------------------------------------------
export default function tabs(tabsSelector, tabsContentSelector, tabsParentSelector, activeClass) {
	const tabs =  document.querySelectorAll(tabsSelector); // '.tabheader__item'
	const tabsContent = document.querySelectorAll(tabsContentSelector); // '.tabcontent'
	const tabsParent = document.querySelector(tabsParentSelector); // '.tabheader__items'
	function hideTabContent() { //    
		tabsContent.forEach(item => { //      forEach()
			// item.style.display = 'none';
			item.classList.add('hide');
			item.classList.remove('show', 'fade'); // toggle()  ,       ,    
		});
		tabs.forEach(item => {
			item.classList.remove(activeClass); // 'tabheader__item_active' -   ,     classList()   
		});
	}
	function showTabContent(i = 0) { // ES6        "0"!!!
		// tabsContent[i].style.display = 'block';
		tabsContent[i].classList.add('show', 'fade');
		tabsContent[i].classList.remove('hide');
		tabs[i].classList.add(activeClass); // 'tabheader__item_active' -   ,     classList()   
	}
	hideTabContent();
	showTabContent();
	tabsParent.addEventListener('click', (event) => { //     tabsParent  '.tabheader__items'  
		const target = event.target; //   event.target    !!!
		if (target && target.classList.contains(tabsSelector.slice(1))) { //           tabs,    ;     '.tabheader__item'
			tabs.forEach((item, i) => { //      forEach(), ..  / - item   i     tabs
				if (target == item) { //        , ..  /    /,    :
					hideTabContent(); // =>       
					showTabContent(i); // =>        
				} //    3- ,   forEach()   ,    ,      ,     'show', 'fade'
			});
		}
	});
}